/*
 * @Description: 输入指定长度整数
 * @Author: Haor
 * @Date: 2021-04-21 08:59:34
 * @LastEditTime: 2021-04-21 18:52:06
 * @LastEditors: weiwei
 * @Example: v-input-integer=“11”
 */
import regular from '@/utils/regular'

// function dealRegexp () {}

export default {
  bind: function (el, binding) {
    const input = el.nodeName === 'INPUT' ? el : el.getElementsByTagName('input')[0]
    const _handleCompositionstartEvent__ = () => { el.inputLocking = true }
    const _handleCompositionendEvent__ = () => {
      el.inputLocking = false
      input.dispatchEvent(new Event('input'))
    }
    const _handleInputEvent__ = (handle) => {
      if (el.inputLocking) return
      // 设置基础常量和变量
      const expression = binding.value // 预设的校验值
      const oldValue = input.value // 旧数据
      let newValue = null // 心的值
      let maxValue = null // 最大值
      let minValue = null // 最小值
      let reg = /[^\d]/g // 正则
      // 判断是否带了修饰符，如果有，则调用对应的正则或对应的方法
      const keys = Object.keys(binding.modifiers) // 获取修饰符字段
      if (keys.length) {
        // 如果修饰符内有内容
        const regName = keys[0]
        if (regName === 'reg') {
          // 如果修饰符是自定义正则，则将 value 设置为正则变量的值
          reg = expression
          newValue = input.value.replace(reg, '')
        } else if (regName === 'number') {
          // 验证是不是数字
          throw Error('还在开发中')
        } else {
          // 判断是否有对应的正则，否则直接抛出错误
          if (regular[regName]) {
            newValue = input.value.replace(regular[regName], '')
          } else {
            throw Error('错误的指令修饰符')
          }
        }
      } else {
        // 如果没有修饰符，就走正常的流程
        const expressionType = typeof expression
        // 处理旧数据以及新数据
        newValue = input.value.replace(reg, '')
        // 根据情况设置对应的值
        if (expressionType === 'number') {
          maxValue = expression
        } else if (expressionType === 'undefined') {
          // 如果没有传递参数
          maxValue = -1
        } else {
          // 处理数据，避免出现多个0的情况
          newValue = String(Number(newValue))
          // 设置最大和最小值
          maxValue = expression[1]
          minValue = expression[0]
        }
        // 当最大值有值的时候表明检查数字范围
        if (newValue && (maxValue || maxValue === undefined) && minValue !== null) {
          if (oldValue === '') {
            // 这里懒得做任何操作
            newValue = ''
          } else {
            // 判断最大值和最小值，如果不符合条件设置为对应的极值
            if (newValue > maxValue) {
              newValue = maxValue
            }
            if (newValue < minValue && handle === 'blur') {
              newValue = minValue
            }
          }
        } else {
          // 否则按照检测长度处理
          newValue = maxValue !== -1 ? newValue.substring(0, maxValue) : newValue
        }
      }
      // 判断是否需要更新，避免进入死循环
      if (newValue !== oldValue) {
        input.value = newValue
        input.dispatchEvent(new Event('input')) // 通知v-model更新
      }
    }
    // 绑定事件
    input._handleCompositionstartEvent__ = _handleCompositionstartEvent__
    input.addEventListener('compositionstart', input._handleCompositionstartEvent__)
    input._handleCompositionendEvent__ = _handleCompositionendEvent__
    input.addEventListener('compositionend', input._handleCompositionendEvent__)
    input._handleInputEvent__ = _handleInputEvent__
    input.addEventListener('input', () => input._handleInputEvent__('input'))
    input.__handleBlurEvent__ = _handleInputEvent__
    input.addEventListener('blur', () => input._handleInputEvent__('blur'))
  },
  unbind (el) {
    const input = el.nodeName === 'INPUT' ? el : el.getElementsByTagName('input')[0]
    input.removeEventListener('compositionstart', input._handleCompositionstartEvent__)
    input.removeEventListener('compositionend', input._handleCompositionendEvent__)
    input.removeEventListener('input', input._handleInputEvent__)
    input.removeEventListener('blur', input.__handleBlurEvent__)
  }
}
