import { get } from '@/utils/request'
const PLATFORM = '/api/v1/platform_service/'

/**
 * 省市区
 * provinceCode cityCode districtCode
 * */
export const getRegion = params => get(`${PLATFORM}address/get_address_by_dynamic_param`, params)

/**
 * 反向查询省市区
 * districtCode
 * */
export const getAddressCodeByDistrict = params => get(`${PLATFORM}address/get_address_code_by_district`, params)

/**
 * 反向查询省市区
 * provinceName
 * cityName
 * districtName
 * */
export const getAddressCodeByName = params => get(`${PLATFORM}address/get_address_code_by_name`, params)
