/*
 * @Description: 点击非自身触发
 * @Author: Haor
 * @Date: 2021-04-22 10:29:31
 * @LastEditTime: 2021-04-22 10:31:02
 * @LastEditors: Haor
 * @Example: v-click-out-side=“this.xxxMethod”
 */
export default {
  /**
   * 绑定指令
   * @param {Element} el 元素
   * @param {Object} binding
   * @param {*} vnode
   */
  bind (el, binding, vnode) {
    const handleClick = e => {
      // 这里判断点击的元素是否是本身，是本身，则返回
      if (el.contains(e.target)) return false

      // 判断指令中是否绑定了函数
      if (binding.expression) {
        // 调用回调函数
        binding.value(e)
      }
    }

    // 给当前元素绑定个私有变量，方便在unbind中可以解除事件监听
    el.__drectiveClickOutside__ = handleClick
    document.addEventListener('click', handleClick)
  },

  /**
   * 移除绑定的时候触发
   * @param {Element}} el
   * @param {*} binding
   */
  unbind (el, binding) {
    // 解除事件监听
    document.removeEventListener('click', el.__drectiveClickOutside__)
    delete el.__drectiveClickOutside__
  }
}
