/*
 * @Description: 权限判断指令
 * @Author: Haor
 * @Date: 2021-04-22 10:32:07
 * @LastEditTime: 2021-04-22 10:33:26
 * @LastEditors: Haor
 * @Example: v-permission=“['USER_ADD']”
 */

import store from '@/store'

export default {
  inserted (el, binding) {
    const { value } = binding
    const permissions = store.getters && store.getters.permissions
    if (value && value instanceof Array && value.length > 0) {
      const permissionValue = value

      const hasPermission = permissions.some(permissionItem => {
        return permissionValue.includes(permissionItem)
      })

      if (!hasPermission) {
        el.parentNode && el.parentNode.removeChild(el)
      }
    } else {
      throw new Error(
        `need roles! Like v-permission="['BTN_USER_CREATE','BTN_USER_MODIFY']"`
      )
    }
  }
}
