import axios from 'axios'
import message from 'ant-design-vue/es/message'
import { VueAxios } from './axios'
import storage from 'store'

// 请求取消
let pending = {} // eslint-disable-line
let requestCount = 0
let msgLoading = null
const CancelToken = axios.CancelToken
const removePending = (key, isRequest = false) => {
  if (pending[key] && isRequest) {
    pending[key]('取消重复请求')
  }
  delete pending[key]
}

// 创建 axios 实例
const request = axios.create({
  timeout: 30000 // 请求超时时间
})

// 异常拦截处理器
const errorHandler = (err) => {
  if (err.response) {
    const data = err.response.data
    // 从 localstorage 获取 token
    switch (err.response.status) {
      case 400:
        err.message = '请求错误(400)'
        break
      case 401:
        err.message = '登陆授权失败(401)'
        break
      case 403:
        err.message = '拒绝访问(403)'
        break
      case 404:
        err.message = '请求出错(404)'
        break
      case 408:
        err.message = '请求超时(408)'
        break
      case 500:
        err.message = '服务器错误(500)'
        break
      case 501:
        err.message = '请求服务器失败(501)'
        break
      case 502:
        err.message = '网络错误(502)'
        break
      case 503:
        err.message = '请求服务器失败(503)'
        break
      case 504:
        err.message = '网络超时(504)'
        break
      case 505:
        err.message = 'HTTP版本不受支持(505)'
        break
      default:
        err.message = err.response.data.message || err.response.data.error
    }
    if (err.response.status === 401 && !(data.result && data.result.isLogin)) {
      err.message = '登录失效'
    }
    message.error(err.message)
  }
  return Promise.reject(err)
}

// request interceptor
request.interceptors.request.use(config => {
  if (config.showLoading) {
    showFullScreenLoading()
  }
  const token = storage.get('Access-Token')
  console.log(token)
  // 如果 token 存在
  // 让每个请求携带自定义 token 请根据实际情况自行修改
  const requestData = config.method === 'get' ? config.param : config.data // 请求的参数 用于取消请求的key拼接
  if (token) {
    config.headers['Authorization'] = `Bearer ${token}`
  }
  const key = `${config.url}&${config.method}&${JSON.stringify(requestData)}`
  removePending(key, config.isRemovePending || true)
  config.cancelToken = new CancelToken(c => {
    pending[key] = c
  })
  return config
}, errorHandler)

// response interceptor
request.interceptors.response.use((res) => {
  if (res.config.showLoading) {
    tryHideFullScreenLoading()
  }
  const requestData = res.config.method === 'get' ? res.config.param : res.config.data // 请求的参数 用于取消请求的key拼接
  const key = `${res.config.url}&${res.config.method}&${JSON.stringify(requestData || {})}`
  const IS_CUSTOMER_MSG = res.config.customerMsg || false // 自定义提示语
  const SUCCESS_CODE = process.env.VUE_APP_HTTP_SUCCESS_CODE // 请求成功code
  const IS_FILE = res.config.responseType === 'arraybuffer'
  removePending(key)
  if (!IS_FILE && !IS_CUSTOMER_MSG && res.data.code !== SUCCESS_CODE) {
    message.error(res.data.message)
    return Promise.reject(res.data.message, res.data)
  }
  return res.data
}, errorHandler)

const installer = {
  vm: {},
  install (Vue) {
    Vue.use(VueAxios, request)
  }
}
function showFullScreenLoading () {
  if (requestCount === 0) {
    msgLoading = message.loading('请求中...', 0)
  }
  requestCount++
}

function tryHideFullScreenLoading () {
  if (requestCount <= 0) return
  requestCount--
  if (requestCount === 0) {
    msgLoading()
  }
}

/**
 * 封装get方法
 * @param url
 * @param data
 * @returns {Promise}
 */

const get = (url, data = {}, options = {}) => {
  return new Promise((resolve, reject) => {
    request
      .get(url, { params: data, ...options })
      .then(response => {
        resolve(response)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
* 封装post请求
* @param url
* @param data
* @returns {Promise}
*/

const post = (url, data = {}, options) => {
  return new Promise((resolve, reject) => {
    request
      .post(url, data, { ...options })
      .then(response => {
        resolve(response)
      })
      .catch(err => {
        reject(err)
      })
  })
}

/**
* 封装put请求
* @param url
* @param data
* @returns {Promise}
*/

const put = (url, data = {}) => {
  return new Promise((resolve, reject) => {
    request
      .put(url, data)
      .then(response => {
        resolve(response.data)
      })
      .catch(err => {
        reject(err)
      })
  })
}

export default request

export {
  installer as VueAxios,
  request as axios,
  get,
  post,
  put
}
